<template>
  <b-card
    tag="article"
     style="width: 30rem; height: 35rem;"
    class="mb-2"
  >
    <img v-if="!isFamilyRecipe" 
         :src="recipe.imageURL"
         class="card-img-top recipe-preview"
         @click="goToRecipe(recipe.id, recipe.title)">
    <img v-else 
         :src="require(`@/assets/${recipe.imageName}`)" 
         class="card-img-top recipe-preview"
         @click="goToRecipe(recipe.id, recipe.title)">
    <b-card-body>
      <b-card-title style="font-weight: bold;"> {{ recipe.title }} </b-card-title>
      <b-card-text>
        <div>
          <p v-if="recipe.owner !== ''"> {{ recipe.owner }}</p>
          <p v-if="recipe.whenUsuallyMade !== ''"> {{ recipe.whenUsuallyMade }}</p>
        </div>
        <div>
        <div style="display: flex;  margin-right: 8px; margin-bottom: 10px; align-items: center;">
          <p><img style="width:20px; height:20px;" src="data:image/png;base64,"> {{ recipe.readyInMinutes }} minutes </p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <p><img style="width:20px; height:20px;" src="data:image/png;base64,">{{ recipe.aggregateLikes }} likes</p>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <b-button variant="link" class="text-decoration-none mb-2" @click="toggleFav">
            <img v-if="!isFav" style="width:20px; height:20px" src="data:image/png;base64," alt="Image 1" >
            <img v-else style="width:20px; height:20px;" src="data:image/png;base64," alt="Image 2">
          </b-button>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <img v-if="recipe.viewed"  src="data:image/png;base64,">
        </div>
        <div style="display: flex; margin-bottom: 7px;">
          <p v-if="recipe.vegetarian" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
          <p v-if="recipe.vegan" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
          <p v-if="recipe.glutenFree" style="margin-right: 10px;"><img src="data:image/png;base64," style="width:20px; height:20px"></p>
          <p v-if="!recipe.glutenFree"><img src="data:image/png;base64,"></p>
        </div>  
      </div>
      </b-card-text>
        </b-card-body>
  <b-alert>
    <p>{{ message }}</p>
  </b-alert>
  </b-card>
</template>

<script>
import { mockAddFavorite } from "../services/user.js";
export default {
  name: 'RecipePriview',
  data() {
    return {
      image_load: false,
      isFav: false,
      message: ""
    };
  },
  props: {
    recipe: {
      type: Object,
      required: true
    },
    isFamilyRecipe:{
      type: Boolean,
      default: false
    },
    isMyRecipe:{
      type: Boolean,
      default: false
    }
  },
  async mounted(){
    const response = await this.axios.get(
          this.$root.store.server_domain + "/users/favoritesID",      
          );
        if (response.status === 401){
          return
        }
        const favorites = response.data;
        console.log(favorites)
        this.isFav = favorites.some(favID =>favID ===this.recipe.id);
        console.log(this.recipe.id)
        console.log(this.isFav)
  },
  methods: {
    goToRecipe(recipeId, title) {
      if (this.isFamilyRecipe){
        this.$router.push({name: 'recipe', params: { recipeId: recipeId, category: "family"}});
      }
      else if (this.isMyRecipe){
        this.$router.push({name: 'recipe', params: { recipeId: title, category: "my"}});
      }
      else{
        this.$router.push({name: 'recipe', params: { recipeId: recipeId, category: "regular"}});
      }
      
      this.recipe.viewed = true
  },
  async toggleFav(event) {
      event.stopPropagation();
      this.isFav = !this.isFav;
      if (this.isFav){
        response = await this.axios.post(
          this.$root.store.server_domain + "/users/favorites",
          {
            recipeId: this.recipe.id,
          }
          
          );
        if (response.status === 200){
          this.message = "The Recipe successfully saved as favorite";
        }
      }
      this.$root.toast("",this.message, "Light ");
    },
  }
};
</script>

<style scoped>
.container {
  font-family: 'Calibri Light', 'Calibri';
  
}
.recipe-preview {
  display: inline-block;
  width: 100%;
  height: 275px;
  cursor: pointer;
}
.recipe-preview:hover {
  filter: blur(2px);
}


</style>
